/**
 *        
 */
export default class Store {
  constructor() {
    this._destinations = [];
    this._offers = [];
  }

  //   
  getDestinations() {
    return this._destinations;
  }

  //   
  getOffers() {
    return this._offers.map((element) => [element.type, element.offers]);
  }

  //   
  setDestinations(destinations) {
    this._destinations = destinations;
  }

  //   
  setOffers(offers) {
    this._offers = offers;
  }
}
