/**
 *   
 */
export default class ModelOffer {
  constructor(offer) {
    this.type = offer.type;
    this.offers = offer.offers.map((item) => {
      return {
        name: item.title,
        price: item.price
      };
    });
  }

  //    
  static parseOffer(offer) {
    return new ModelOffer(offer);
  }

  //    
  static parseOffers(offers) {
    return offers.map(ModelOffer.parseOffer);
  }
}
